___CSS_LOADER_EXPORT___.push([module.id, ".fade:not(.show){opacity:1}.vue__time-picker.time-picker{width:100%}", "",{"version":3,"sources":["webpack://./resources/js/components/pickup/CreatePickupPoint.vue"],"names":[],"mappings":"AAuRA,iBACA,SACA,CACA,8BACA,UACA","sourcesContent":["<template>\r\n  <div class=\"modal\" id=\"create-pickup-point\">\r\n    <div class=\"modal-dialog\">\r\n      <div class=\"modal-content\">\r\n        <!-- Modal Header -->\r\n        <div class=\"modal-header\">\r\n          <h4 class=\"modal-title\">Create Pickup Point</h4>\r\n          <button type=\"button\" class=\"close\" data-dismiss=\"modal\">\r\n            &times;\r\n          </button>\r\n        </div>\r\n\r\n        <!-- Modal body -->\r\n        <div class=\"modal-body\">\r\n          <validation-observer ref=\"createPickupPoint\">\r\n            <form @submit.prevent=\"createPickupPoint\" ref=\"form\">\r\n              <div class=\"form-row\">\r\n                <div class=\"col-md-12\">\r\n                  <validation-provider\r\n                    name=\"Bus\"\r\n                    :rules=\"{ required: true }\"\r\n                    v-slot=\"validationContext\"\r\n                  >\r\n                    <div class=\"form-group\">\r\n                      <label for=\"name\">Route</label>\r\n                      <select2\r\n                        id=\"amenities\"\r\n                        aria-describedby=\"bus-feedback\"\r\n                        v-model.trim=\"pickupPoint.route_id\"\r\n                        :options=\"routes\"\r\n                        :settings=\"{\r\n                          theme: 'bootstrap',\r\n                          multiple: false,\r\n                          closeOnSelect: true,\r\n                        }\"\r\n                        required\r\n                      >\r\n                      </select2>\r\n                      <div id=\"route-feedback\" class=\"invalid-feedback w-100\">\r\n                        {{ validationContext.errors[0] }}\r\n                      </div>\r\n                    </div>\r\n                  </validation-provider>\r\n\r\n                  <validation-provider\r\n                    name=\"Pickup Point\"\r\n                    :rules=\"{ required: true, min: 2, max: 20 }\"\r\n                    v-slot=\"validationContext\"\r\n                  >\r\n                    <div class=\"form-group\">\r\n                      <label for=\"name\">Pickup Point</label>\r\n                      <input\r\n                        type=\"text\"\r\n                        placeholder=\"Enter pickup point\"\r\n                        aria-describedby=\"Pickup-Point-feedback\"\r\n                        v-model.trim=\"pickupPoint.pickup_point\"\r\n                        :class=\"`form-control ${getValidationState(\r\n                          validationContext\r\n                        )}`\"\r\n                      />\r\n                      <div\r\n                        id=\"Pickup-Point-feedback\"\r\n                        class=\"invalid-feedback w-100\"\r\n                      >\r\n                        {{ validationContext.errors[0] }}\r\n                      </div>\r\n                    </div>\r\n                  </validation-provider>\r\n\r\n                  <validation-provider\r\n                    name=\"Pickup Time\"\r\n                    :rules=\"{ required: true }\"\r\n                    v-slot=\"validationContext\"\r\n                  >\r\n                    <div class=\"form-group\">\r\n                      <label for=\"board-time\">Pickup Time</label>\r\n                      <vue-timepicker\r\n                        format=\"HH:mm\"\r\n                        aria-describedby=\"drop-time-feedback\"\r\n                        v-model.trim=\"pickupPoint.pickup_time\"\r\n                        :input-class=\"`form-control w-100 ${getValidationState(\r\n                          validationContext\r\n                        )}`\"\r\n                        :minute-interval=\"5\"\r\n                        manual-input\r\n                      />\r\n\r\n                      <div\r\n                        id=\"board-time-feedback\"\r\n                        :class=\"`invalid-feedback w-100 ${\r\n                          validationContext.errors[0] ? 'd-block' : ''\r\n                        }`\"\r\n                      >\r\n                        {{ validationContext.errors[0] }}\r\n                      </div>\r\n                    </div>\r\n                  </validation-provider>\r\n\r\n                  <validation-provider\r\n                    name=\"Address\"\r\n                    :rules=\"{ required: false }\"\r\n                    v-slot=\"validationContext\"\r\n                  >\r\n                    <div class=\"form-group\">\r\n                      <label for=\"name\">Address</label>\r\n                      <input\r\n                        type=\"text\"\r\n                        placeholder=\"Enter pickup point address\"\r\n                        aria-describedby=\"Pickup-Point-feedback\"\r\n                        v-model.trim=\"pickupPoint.address\"\r\n                        :class=\"`form-control ${getValidationState(\r\n                          validationContext\r\n                        )}`\"\r\n                      />\r\n                      <div id=\"address-feedback\" class=\"invalid-feedback w-100\">\r\n                        {{ validationContext.errors[0] }}\r\n                      </div>\r\n                    </div>\r\n                  </validation-provider>\r\n\r\n                  <validation-provider\r\n                    name=\"Landmark\"\r\n                    :rules=\"{ required: false }\"\r\n                    v-slot=\"validationContext\"\r\n                  >\r\n                    <div class=\"form-group\">\r\n                      <label for=\"name\">Landmark</label>\r\n                      <input\r\n                        type=\"text\"\r\n                        placeholder=\"Enter pickup point nearest landmark\"\r\n                        aria-describedby=\"Pickup-Point-feedback\"\r\n                        v-model.trim=\"pickupPoint.landmark\"\r\n                        :class=\"`form-control ${getValidationState(\r\n                          validationContext\r\n                        )}`\"\r\n                      />\r\n                      <div\r\n                        id=\"landmark-feedback\"\r\n                        class=\"invalid-feedback w-100\"\r\n                      >\r\n                        {{ validationContext.errors[0] }}\r\n                      </div>\r\n                    </div>\r\n                  </validation-provider>\r\n\r\n                  <div class=\"form-group\">\r\n                    <div class=\"form-check\">\r\n                      <label class=\"form-check-label\">\r\n                        <input\r\n                          class=\"form-check-input\"\r\n                          type=\"checkbox\"\r\n                          id=\"status\"\r\n                          v-model=\"pickupPoint.status\"\r\n                        />\r\n                        Status\r\n                      </label>\r\n                    </div>\r\n                  </div>\r\n                </div>\r\n              </div>\r\n\r\n              <div class=\"col-12 my-3 text-right\">\r\n                <button class=\"btn btn-primary\" type=\"submit\">Submit</button>\r\n              </div>\r\n            </form>\r\n          </validation-observer>\r\n        </div>\r\n      </div>\r\n    </div>\r\n  </div>\r\n</template>\r\n\r\n<script>\r\nimport VueTimepicker from \"vue2-timepicker\";\r\nimport \"vue2-timepicker/dist/VueTimepicker.css\";\r\nimport Select2 from \"v-select2-component\";\r\nimport \"select2-bootstrap-theme/dist/select2-bootstrap.min.css\";\r\n\r\nexport default {\r\n  components: { VueTimepicker, Select2 },\r\n  props: {\r\n    defaultRoute: {\r\n      default: \"\",\r\n    },\r\n  },\r\n  emits: [\"update\"],\r\n  data() {\r\n    return {\r\n      pickupPoint: {\r\n        route_id: this.defaultRoute,\r\n        status: 1,\r\n        pickup_time: \"\",\r\n      },\r\n      routes: [],\r\n    };\r\n  },\r\n  methods: {\r\n    resetForm() {\r\n      this.pickupPoint = {\r\n        route_id: this.defaultRoute,\r\n        status: 1,\r\n        pickup_time: \"\",\r\n      };\r\n      this.$nextTick(() => {\r\n        this.$refs.createPickupPoint.reset();\r\n      });\r\n    },\r\n    createPickupPoint() {\r\n      this.$refs.createPickupPoint.validate().then((valid) => {\r\n        if (valid) {\r\n          //submit data\r\n          this.$store.dispatch(\"startLoading\");\r\n          axios\r\n            .post(\"/admin/pickup-points\", this.pickupPoint)\r\n            .then((res) => {\r\n              //   this.$store.dispatch(\"stopLoading\");\r\n              this.closeModal();\r\n\r\n              this.$nextTick(() => {\r\n                this.$emit(\"update\");\r\n                this.showSuccessToast(\"Pickup point created successfully\");\r\n              });\r\n            })\r\n            .catch((res) => {\r\n              this.$store.dispatch(\"stopLoading\");\r\n              this.showErrorToast(\r\n                \"There was an error creating the pickup point\"\r\n              );\r\n            });\r\n        }\r\n      });\r\n    },\r\n    fetchRoutes(pageLoad = true) {\r\n      this.$store.dispatch(\"startLoading\");\r\n      this.isLoading = pageLoad;\r\n\r\n      axios\r\n        .get(\"/admin/routes\")\r\n        .then((res) => {\r\n          this.routes = [];\r\n\r\n          res.data.data.map((route) => {\r\n            this.routes.push({\r\n              id: route.id,\r\n              text: `${route.bus.formatted_name} ${route.route_name}`\r\n            });\r\n          });\r\n\r\n          this.$store.dispatch(\"stopLoading\");\r\n          this.isLoading = false;\r\n        })\r\n        .catch((res) => {\r\n          this.$store.dispatch(\"stopLoading\");\r\n          this.isLoading = false;\r\n        });\r\n    },\r\n    //---- Validation State Form\r\n    getValidationState({ dirty, validated, valid = null }) {\r\n      if (dirty || validated) {\r\n        return valid ? \"is-valid\" : \"is-invalid\";\r\n      }\r\n      return \"\";\r\n    },\r\n    closeModal() {\r\n      $(\"#create-pickup-point\").modal(\"hide\");\r\n    },\r\n  },\r\n  created() {\r\n    const self = this;\r\n    self.fetchRoutes();\r\n    $(function () {\r\n      $(\"#create-pickup-point\").on(\"show.bs.modal\", function (e) {\r\n        self.resetForm();\r\n      });\r\n    });\r\n  },\r\n};\r\n</script>\r\n<style>\r\n.fade:not(.show) {\r\n  opacity: 1;\r\n}\r\n.vue__time-picker.time-picker {\r\n  width: 100%;\r\n}\r\n</style>\r\n"],"sourceRoot":""}]);
